@charset "UTF-8";

@import "import/mixins";
@import "import/functions";
@import "import/variables";

/* tab */
.tab-area {
    position: relative; }

.tab-area .tab-linker {
position: relative;
overflow-x: auto;
overflow-y: hidden;
-webkit-overflow-scrolling: touch; }

.tab-area .tab-linker > ul {
position: relative;
background-color: #fff;
overflow: hidden;
overflow-x: auto;
-webkit-overflow-scrolling: touch; }

.tab-area .tab-linker > ul:after {
content: "";
position: absolute;
left: 0;
right: 0;
bottom: 0;
border-bottom: 1px solid #f1f1f1; }

.tab-area .tab-linker > ul > li {
position: relative;
float: left;
margin-left: 1rem;
border-bottom: 0.13rem solid transparent;
height: 3rem;
-webkit-box-sizing: border-box;
        box-sizing: border-box;
z-index: 5; }

.tab-area .tab-linker > ul > li button, .tab-area .tab-linker > ul > li a {
display: inline-block;
position: relative;
height: 2.81rem;
line-height: 2.81rem;
font-size: 0.88rem;
color: #757575;
font-weight: 400;
vertical-align: middle; }

.tab-area .tab-linker > ul > li button .count, .tab-area .tab-linker > ul > li a .count {
display: inline-block;
-webkit-box-sizing: border-box;
        box-sizing: border-box;
margin: 0 0 0 0.38rem;
width: 0.88rem;
height: 0.88rem;
border-radius: 100%;
background-color: #178BCE;
font-size: 0.63rem;
line-height: 0.88rem;
color: #fff;
font-weight: 700;
text-align: center;
vertical-align: baseline; }

.tab-area .tab-linker > ul > li button.tip-view, .tab-area .tab-linker > ul > li a.tip-view {
position: relative; }

.tab-area .tab-linker > ul > li button.tip-view:after, .tab-area .tab-linker > ul > li a.tip-view:after {
content: "";
position: absolute;
left: 0;
right: 0;
bottom: -0.19rem;
border-top: 0.19rem solid #fff;
border-bottom: 1px solid #f1f1f1; }

.tab-area .tab-linker > ul > li button.disabled, .tab-area .tab-linker > ul > li a.disabled {
color: #dcdcdc;
pointer-events: none; }

.tab-area .tab-linker > ul > li button:disabled, .tab-area .tab-linker > ul > li a:disabled {
color: #dcdcdc;
pointer-events: none; }

.tab-area .tab-linker > ul > li[aria-selected="true"] {
border-bottom: 0.19rem solid #178BCE;
color: #000;
font-weight: 400;
}

.tab-area .tab-linker > ul > li[aria-selected="true"] button, .tab-area .tab-linker > ul > li[aria-selected="true"] a {
font-weight: 700;
color: #178BCE; }

.tab-area .tab-linker > ul.tab-single {
border-bottom: 0 none;
margin-bottom: -0.75rem;
margin-top: -0.63rem; }

.tab-area .tab-linker > ul.tab-single li {
height: 2.81rem; }

.tab-area .tab-linker > ul.tab-single li[aria-selected="true"] {
border-bottom: 0 none !important; }

.tab-area .tab-linker > ul.tab-single li[aria-selected="true"] button, .tab-area .tab-linker > ul.tab-single li[aria-selected="true"] a {
height: 2.81rem;
line-height: 2.81rem;
font-size: 0.94rem; }

.tab-area .tab-linker .tab-style01:after {
border-bottom: 0 !important; }

.tab-area .tab-contents > ul:after {
content: "";
display: block;
clear: both; }

.tab-area .tab-contents > ul > li {
float: left;
width: 100%;
height: 0;
overflow: hidden;
display: none; }

.tab-area .tab-contents > ul > li[aria-selected="true"] {
height: auto;
display: block; }

.tab-area.new01 .tab-linker ul {
    display: table;
    table-layout: fixed;
    width: 100%; }

.tab-area.new01 .tab-linker ul li {
    display: table-cell;
    width: 50%;
    margin: 0;
    text-align: center;
    float: none;
    height: 2.5rem; }

.tab-area.new01 .tab-linker ul li a,
.tab-area.new01 .tab-linker ul li button {
    font-size: 0.94rem;
    color: #959595;
    font-weight: 400 !important;
    height: 2.5rem;
    line-height: 2.38rem; }/* 190831 리디자인 : button 추가 */

.tab-area.new01 .tab-linker ul li[aria-selected="true"] {
    border-bottom: 2px solid #ef4b49; }

.tab-area.new01 .tab-linker ul li[aria-selected="true"] a,
.tab-area.new01 .tab-linker ul li[aria-selected="true"] button{
    color: #ef4b49 !important; }

/* main */
body{
    .container-wrap{
        margin-bottom:rem(20px);
    }
    &.apphome{
        .container-wrap{
            background: linear-gradient(#fff, #f5f5f5 30%);
        }
    }
}

.scroll .h-belt {
    box-shadow: none;
}

.header-wrap.home .h-belt{
    border-bottom: 1px solid #f2f2f2!important;
}

.fly .header-wrap .h-belt .h-default .h-logo {
    top: 0.31rem;
    opacity: 1;
}

.icon-gnb-menu{
    background-image: url("/img/en/icon_48px_titlebar_menu.svg");
}

.icon-gnb-home-T {
    background-image: url("/img/en/graphic_80px_bi_skt.svg");
}

/* 로그인 전 */
.section-box{
    &.mw{
        margin-top:rem(-45px);
        .main-error-box.welcome.tod-login-box{
            padding:rem(28px 0);
        }
    }
}

.main-error-box.welcome.tod-login-box{
    padding:rem(65px 0 25px 0);
    .ti{
        font-size: rem(23px);
        margin-top:0;
    }
    .tx{
        font-size: rem(13px);
        margin-top:rem(18px);
        line-height:1.4em;
        > span{
            color:#f76252;
        }
    }
    .btn-area{
        margin-top:rem(23px);
    }
    .desc2{
        position:static;
        margin-top:rem(26px);
    }
    &.line-none{
        .btn-area{
            .btn-style1{
                width:rem(160px);
                font-size: rem(15px);
            }
        }
        .desc2{
            font-size: rem(14px);
            line-height:1.6em;
            .bt{
                font-size: rem(15px);
                margin:0 auto;
            }
        }
    }
}

/* 로그인 후 */
.mw-banner01{
    button{
        width:100%;
        img{
            width:100%;
        }
    }
}

.section-box.mw{
    .main-chart-box{
        padding:rem(25px 0);
    }
}

.main-top-group {
    margin-bottom: 0;
    background: transparent;
    &.internet{
        padding:rem(0 24px);
    }
}

body.apphome{
    .main-top-group::before {
        display:none;
    }
    .error-code{
        padding-bottom:rem(30px);
    }
}

.main-chart-box{
    padding-top:rem(42px);
    padding-bottom:rem(12px);
    > .ti{
        margin-top:rem(12px);
        .bt{
            font-size: rem(24px);
        background-image: url("/img/en/chart_tit_arrow.png");
        }
    }
}

.main-chart-box .donutchart .donut-chart.data-type .ti-data{
    margin-top:rem(-8px);
    .ti{
        background-image: url("/img/en/chart-data-ar.png");
        &.limited{
            line-height:1.2em;
            background-position:right rem(3px) bottom rem(4px);
        }
    }
}

.donut-chart {
    background-image: url("/img/en/main/bg-donut.png");
}

.donut-chart .dot {
    background-image: url("/img/en/main/bg_dot.png");
}

.donut-chart .highlight {
    background-image: url("/img/en/main/graph_highlight.png");
}

.icon-call-34 {
    background-image: url("/img/en/icon_34px_call.png");
}

.icon-sms-37 {
    background-image: url("/img/en/icon_37px_sms.png");
}

.main-charge-box.section-swiper-box {
    min-height: auto;
}

.main-fee-box{
    .fee-cont{
        &.plan-slide{
            display:block;
            height:auto;
            .slider{
                width:calc(100% + 3rem);
                padding:rem(25px 0 40px 24px);
                margin-left:rem(-24px);
                li{
                    font-size: 0;
                    > .plan-card{
                        position:relative;
                        display:inline-block!important;
                        vertical-align: top;
                        width:calc((100% - 44px) / 2);
                        height:rem(185px);
                        border-radius:rem(15px);
                        text-align: left;
                        color:#fff;
                        background-color:#cb3250;
                        box-sizing: border-box;
                        padding:rem(21px 15px);
                        &.right{
                            margin-left:20px;
                            background-color:#f0682e;
                            p.price{
                                border-top:1px solid #ed5c26;
                            }
                        }
                        > div{
                            position:absolute;
                            top:rem(21px);
                            left:rem(15px);
                        }
                        p{
                            font-size: rem(13px);
                            font-weight: 500;
                            &.plan-name{
                                line-height:1.2em;
                                > strong{
                                    display:block;
                                    font-size: rem(17px);
                                    font-weight: 900;
                                }
                            }
                            &.data{
                                padding-left:rem(26px);
                                margin-top:rem(10px);
                                background:url("/img/en/main/icon_plan_data.png") left center no-repeat;
                                background-size:rem(13px 13px);
                            }
                            &.sms{
                                padding-left:rem(26px);
                                margin-top:rem(8px);
                                background:url("/img/en/main/icon_plan_sms.png") left center no-repeat;
                                background-size:rem(14px 10px);
                            }
                            &.call{
                                padding-left:rem(26px);
                                margin-top:rem(8px);
                                background:url("/img/en/main/icon_plan_call.png") left center no-repeat;
                                background-size:rem(14px 14px);
                            }
                            &.price{
                                position:absolute;
                                bottom:0;
                                left:0;
                                width:100%;
                                height:rem(42px);
                                line-height:rem(41px);
                                font-size: rem(16px);
                                font-weight: 700;
                                text-align: center;
                                border-top:1px solid #c32a45;
                                > span{
                                    font-size: rem(14px);
                                }
                            }
                        }
                    }
                }
                .slick-prev, .slick-next{
                    display:none!important;
                }
                .slick-dots{
                    bottom:rem(17px);
                    margin-left:rem(-24px);
                    > li{
                        width:rem(8px);
                        height:rem(8px);
                        > button{
                            width:rem(8px);
                            height:rem(8px);
                            border-radius: 50%;
                            background-color:transparent;
                            border:1px solid #000;
                        }
                        &.slick-active{
                            > button{
                                background-color:#000;
                            }
                        }
                    }
                }
            }
        }
        > .tit{
            font-size: rem(26px);
            padding-bottom:rem(35px);
            & + .fee-info{
                padding-top:0;
                &.support{
                    background-position:right rem(3px);
                }
            }
        }
        .fee-info{
            &:last-child{
                padding-bottom:0.94rem;
                &.no-billing{
                    padding-bottom:rem(27px);
                }
            }
            &.no-billing{
                font-size: rem(15px);
                font-weight: 300;
                padding-bottom:rem(35px);
                cursor: normal;
                pointer-events: none;
            }
            &.no-events{
                cursor: normal;
                pointer-events: none;
            }
            .target{
                .tit{
                    font-size: rem(15px);
                    font-weight: 500;
                    .logo-miri{
                        display:inline-block;
                        width:rem(38px);
                        height:rem(16px);
                        background:url("/img/en/main/logo_miri.png") center center no-repeat;
                        background-size:100% 100%;
                        vertical-align: top;
                    }
                }
                .date{
                    font-size: rem(13px);
                    color:#858585;
                    font-weight: 300;
                }
            }
            .sum{
                font-size: rem(15px);
                &.monthly-fee{
                    color:#f76252;
                }
            }
            &.support{
                background:url("/img/en/main/arrow_support.png") right rem(24px) no-repeat;
                background-size:rem(7px 11px);
                > p{
                    font-size: rem(15px);
                    font-weight: 500;
                    color:#000;
                    text-align: left;
                    line-height:1.4em;
                    padding-left:rem(40px);
                    padding-right:rem(25px);
                    background-position:left center;
                    background-repeat:no-repeat;
                    > span{
                        display:block;
                        font-size: rem(13px);
                        font-weight: 300;
                        color:#858585;
                        line-height:1.2em;
                    }
                    &#support01{
                        background-image:url("/img/en/main/icon_support01.png");
                        background-size:rem(26px 26px);
                    }
                    &#support02{
                        background-image:url("/img/en/main/icon_support02.png");
                        background-size:rem(24px 26px);
                    }
                    &#support03{
                        background-position:rem(3px) center;
                        background-image:url("/img/en/main/icon_support03.png");
                        background-size:rem(19px 28px);
                    }
                }
            }
        }
        .fee-info + .fee-info{
            border-top:1px solid #f2f2f2;
        }
        .tabs{
            margin-top:rem(-5px);
            .tab-area.new01{
                .tab-linker{
                    > ul{
                        > li{
                            height:rem(28px);
                            > a{
                                position:relative;
                                height:rem(28px);
                                line-height:rem(26px);
                                font-size: rem(14px);
                                font-weight: 500!important;
                                color:#c0bdbd;
                            }
                            &[aria-selected="true"]{
                                > a{
                                    font-weight: 700!important;
                                    &::before{
                                        position:absolute;
                                        display:block;
                                        content:'';
                                        top:rem(3px);
                                        left:rem(-6px);
                                        width:rem(4px);
                                        height:rem(4px);
                                        background-color:#ef4b49;
                                        border-radius: 50%;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .btn-area{
        .bt{
            font-weight: 700;
        }
    }
}

.internet-box{
    .fee-info{
        padding:rem(8px 0 20px 0);
        > p{
            font-size: rem(15px);
            font-weight: 500;
            color:#000;
            &.price{
                font-size: rem(23px);
                font-weight: 700;
                color:#f76252;
                margin-top:rem(6px);
            }
            &.date{
                font-size: rem(13px);
                font-weight: 300;
                color:#959595;
                margin-top:rem(5px);
            }
        }
        .btn-area{
            margin-top:rem(20px);
            .btn-style1{
                width:rem(160px);
            }
        }
    }
    .fee-detail{
        text-align: left;
        .top{
            padding-top:rem(18px);
            border-top:1px solid #f2f2f2;
            > p + p{
                margin-top:rem(8px);
            }
        }
        p{
            font-size: rem(15px);
            font-weight: 500;
            color:#000;
            .price{
                float:right;
                font-weight: 700;
                &.discount{
                    color:#f76252;
                }
            }
            .logo-miri{
                display:inline-block;
                width:rem(38px);
                height:rem(16px);
                background:url("/img/en/main/logo_miri.png") center center no-repeat;
                background-size:100% 100%;
                vertical-align: top;
            }
        }
        .miri{
            padding-top:rem(20px);
            border-top:1px solid #f2f2f2;
        }
        .top + .miri{
            margin-top:rem(18px);
        }
    }
    .no-bill{
        padding:rem(50px 0);
        > p{
            font-size: rem(15px);
            font-weight: 500;
            color:#000;
        }
    }
}

.error-code{
    padding-top:rem(95px);
    background:url("/img/en/ico_notice_point_gray.png") center top rem(20px) no-repeat;
    background-size:rem(60px 60px);
    > p{
        font-size: rem(14px);
        font-weight: 300;
        color:#000;
        &.error-title{
            font-size: rem(21px);
            font-weight: 500;
            margin-bottom:rem(20px);
        }
    }
}
